<template lang="html">
  <div class="detail-line-box">
    <div class="detail-line-left">
      {{message}}:
      <span class="score">{{score}}</span>
    </div>
    <div class="detail-line-right" v-if="false">
      <a href="##">
        详情&gt;&gt;
      </a>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    message: {
      type: String,
      required: true
    },
    score: {
      type: Number | String,
      default: 0
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
.detail-line-box{
  color: @defaultColor;
  font-size: .32rem;
  height: 1.6rem;
  display: table;
  width: 100%;
  table-layout: fixed;
  border-bottom: 1px solid @lineColor;
  .detail-line-left {
    display: table-cell;
    vertical-align: middle;
    font-size: .32rem;
    text-align: left;
    color: @defaultColor;
    .score {
      color: @redColor;
    }
  }
  .detail-line-right {
    display: table-cell;
    vertical-align: middle;
    width: 1.8rem;
    a {
      display: inline-block;
      width: 100%;
      height: .88rem;
      line-height: .88rem;
      text-align: center;
      font-size: .34rem;
      color: @midleColor;
      border: 1px solid @lightColor;
      border-radius:5%;
    }
  }
}
</style>



