<template lang="html">
  <div class="">
    <default-head header="商家评价总分"></default-head>
    <div id="wrapper" class="score-info-box">
      <detail-line message="服务评价平均得分" :score="this.preRate"></detail-line>
      <detail-line style="border-bottom: none" message="接通率" :score="connectRate"></detail-line>
      <sub-button v-if="false" class="score-foot-btn" :text="this.totalRate"></sub-button>
      <router-link v-if="false" class="rules" to="/rankrules" @click.native="$router.push('/rankrules')">得分运算规则？</router-link>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import defaultHead from '@/components/head/defaultHead'
import detailLine from '@/components/shopTotalScore/detailLine'
import subButton from '@/components/common/subButton'
export default {
  components: { defaultHead, detailLine, subButton },
  data () {
    return {
      scoreInfo: {
        preScore: 0.00,
        passRate: 0.00,
        totalRate: 0.00
      }
    }
  },
  computed: {
    userId: function () {
      return utils.userId()
    },
    preRate () {
      let num = parseFloat(this.scoreInfo.preScore)
      return parseFloat(num).toFixed(2) + '%'
    },
    connectRate () {
      let num = parseFloat(this.scoreInfo.passRate)
      return parseFloat(num).toFixed(2) + '%'
    },
    totalRate () {
      let num = parseFloat(this.scoreInfo.totalRate)
      return '综合得分' + parseFloat(num).toFixed(2) + '分'
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      this.ajaxFindByUserId({userId: this.userId})
    },
    ajaxFindByUserId (data) {
      api.findByUserId(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.scoreInfo = result.data
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.score-info-box {
  background:@whiteColor;
  padding: 0 .22rem .65rem .22rem;
  text-align: center;
  .rules {
    display: inline-block;;
    text-decoration: underline;
    color: @midleColor;
    margin-top: .3rem;
    font-size: .32rem;
  }
}
.score-foot-btn {
  margin-top: .4rem;
}
</style>



